.how_do {
	.filter-btn {
		margin-bottom: 0;
		margin-right: 14px;
	}
	.showcase_filter{
		padding: 10px;
		border-radius: 40px;
		background: var(--Body-Light-Grey, #F9F9F9);
		margin-bottom: 29px;
	}
	.showcase_title{
		padding-bottom: 24px;
	}
	.showcase_top {
		padding: 107px 0 0 0;
	}

	.how_do_swiper {
		position: relative;
		width: 100vw;
		margin-left: calc(50% - 50vw);
		padding-left: calc((100vw - 1200px) / 2);
		overflow: hidden;
		padding-bottom: 116px;

		.swiper-wrapper {
			display: flex;

		}

		.how_do_swiper__item {
			flex-shrink: 0;
			max-width: 384px;
			height: 384px;

			a {
				opacity: 1;
			}

			.how_do__img {
				position: relative;
				height: 100%;
				overflow: hidden;

				img {
					width: 100%;
					height: 100%;
					border-radius: 16px;
					object-fit: cover;
				}

				.how_do__title {
					position: absolute;
					bottom: 20px;
					left: 20px;
					padding: 10px 20px;
					border-radius: 32px;
					background: var(--Body-Black, #1A1A1A);
					color: var(--Text-Inverse, #FFF);
					font-family: 'Montserrat', sans-serif;
					font-size: 14px;
					font-weight: 700;
					line-height: 100%;
				}
			}
		}

		.swiper-pagination {
			position: absolute;
			bottom: 65px;
			left: 0;
			width: 100%;
			display: flex;
			justify-content: center;
			gap: 12px;

			.swiper-pagination-bullet {
				width: 12px;
				height: 12px;
				border-radius: 50%;
				background: #959595;
				opacity: 0.4;
				position: relative;
				z-index: 1;
			}

			&::after {
				content: '';
				position: absolute;
				top: 50%;
				height: 13px;
				border-radius: 6px;
				background: var(--Text-Secondary, #434343);
				transform: translateY(-50%);
				transition: all 0.15s ease-in-out;
				z-index: 2;

				left: var(--bullet-left, 0);
				width: var(--bullet-width, 12px);
			}
		}
	}
}
